
import Calendar from 'vux/src/components/calendar/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
components: { Calendar, Group },
props: {
startDate: {
type: String
},
endDate: {
type: String
}
},
data () {
return {
startDatecur: this.startDate,
endDatecur: this.endDate
}
},
methods: {
dateSearch (event) {
this.$emit('search-click', event.target)
},
startDateChange (val) {

this.$emit('update:startDate', val)
},
endDateChange (val) {

this.$emit('update:endDate', val)
}
}
}




