import BASE_PATH from '@/api/basePath'
const API = {
  // 
  MY_VACATION_LIST (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppAction!queryLeaveInfo.do', params)
  },
  // 
  IS_WORK_DAY (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppAction!judgeWorkingDay.do', params)
  },
  // -
  GET_VACATION_PERMIT (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppAction!getQjHomePage.do', params)
  },
  // 
  GET_VACATION_TYPE (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppAction!queryQjType.do', params)
  },
  // -
  WINDOW_GET_HOURS (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppAction!calculationQJWorkHours.do', params)
  },
  // -
  WINDOW_APPLY_VACATION (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppAction!saveApplicationLeave.do', params)
  },
  // --
  WINDOW_GET_CHECK_LIST (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppShAction!queryAll.do', params)
  },
  // /--
  WINDOW_GET_CHECK_DETAIL (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppShAction!queryInfoById.do', params)
  },
  // -
  CHECK_PASS (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppShAction!saveAdopt.do', params)
  },
  // -
  CHECK_REFUSE (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppShAction!saveReject.do', params)
  },
  // ---
  DEPT_GET_DAYS (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppAction!queryDay.do', params)
  },
  // --
  DEPT_APPLY_VACATION (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppAction!saveApplicationLeaveSpj.do', params)
  },
  // --
  DEPT_GET_CHECK_LIST (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppShAction!querySpjAll.do', params)
  }

}
export default API



